import React from "react"

import * as Scroll from "react-scroll"
import { Container, Icons, ImgStyled, SkillsContainer } from "../styles/styledHome"
import { FaLinkedin, FaGithubSquare, FaReact, FaNodeJs, } from "react-icons/fa"
import { SiJavascript, SiTypescript, SiMongodb } from "react-icons/si"
import ProfilePic from "../assets/foto.png"
import Resume from "../assets/resume.pdf"
import Pokedex from "../assets/pokedex2.png"
import Weather from "../assets/weather.png"

const Home = () => {

    const Link = Scroll.Link

    return(
        <Container>
            <div id="profile">
                <img src={ProfilePic}/>
                <Link to="about" smooth={true} duration={200}>About</Link>
                <Link to="education" smooth={true} duration={200}>Education</Link>
                <Link to="skills" smooth={true} duration={200}>Skills</Link>
                <Link to="publications" smooth={true} duration={200}>Publications</Link>
            </div>

            <div id="about">
                <div>
                    <div id="profile_content">
                        <h3>Under construction...</h3>
                        <h1>Gustavo Charamba</h1>
                        <p>Web Developer - Full-Stack</p>
                        <a href={Resume} download>Download Resume</a>
                        <div>
                            <a href="https://www.linkedin.com/in/gustavocharamba/"><FaLinkedin style={Icons}/></a>
                            <a href="https://github.com/advaner"><FaGithubSquare style={Icons}/></a>
                        </div>
                    </div>
                </div>
            </div>
            <div id="education">
                <div>
                    <div id="education_content">
                        <h1>Education</h1>
                        <ul>
                            <h3>High School</h3>
                            <li>
                                Cólegio Evolução
                            </li>

                            <h3>Graduation</h3>
                            <li>
                                Engenharia Eletrônica - Universidade Federal de Pernambuco (Graduating)
                            </li>
                        </ul>
                        <h1>Languages</h1>
                        <ul>
                            <li>
                                Portuguese - Native
                            </li>
                            <li>
                                English - Expert
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div id="skills">
                <div>
                    <div id="skills_content">
                        <h1>Skills</h1>
                        <SkillsContainer>
                            <div>
                                <SiJavascript style={{width: "75px", height: "100px", color: "#f0db4f"}}/>
                                <h4>Javascript</h4>
                            </div>
                            <div>
                                <SiMongodb style={{width: "75px", height: "100px", color: "#3FA037"}}/>
                                <h4>MongoDB</h4>
                            </div>
                            <div>
                                <SiTypescript style={{width: "75px", height: "100px", color: "#007acc"}}/>
                                <h4>Typescript</h4>
                            </div>
                            <div>
                                <FaReact style={{width: "75px", height: "100px", color: "#61DBFB"}}/>
                                <h4>React JS</h4>
                            </div>
                            <div>
                                <FaReact style={{width: "75px", height: "100px", color: "#61DBFB"}}/>
                                <h4>React Native</h4>
                            </div>
                            <div>
                                <FaNodeJs style={{width: "75px", height: "100px", color: "#3C873A"}}/>
                                <h4>NodeJS</h4>
                            </div>
                        </SkillsContainer>
                    </div>
                </div>
            </div>

            <div id="publications">
                <div>
                    <div id="publications_content">
                        <h1>Publications</h1>

                        <div>
                            <h2>Pokedex</h2>
                            <a href="https://github.com/advaner/pokedex">See on github.</a>
                            <p>My first personal project, i did to just for fun and put what i was studying in pratice. Made with PokéAPI.</p>
                            <div>
                                <h3>Used skills</h3>
                                <div>
                                    <SiJavascript style={{width: "50px", height: "75px", color: "#f0db4f"}}/>
                                    <SiTypescript style={{width: "50px", height: "75px", color: "#007acc"}}/>
                                    <FaReact style={{width: "50px", height: "75px", color: "#61DBFB"}}/>
                                </div>
                                <h3>Used libraries</h3>
                                <ul>
                                    <li>axios</li>
                                    <li>react-router-dom</li>
                                </ul>
                                <div className="public_img_div">
                                    <img src={Pokedex} style={ImgStyled}></img>
                                </div>
                            </div>
                        </div>



                        <div>
                            <h2>Weather</h2>
                            <a href="https://github.com/advaner/weather-web">See on github.</a>
                            <p>Teste</p>
                            <div>
                                <h3>Used skills</h3>
                                <div>
                                    <SiJavascript style={{width: "50px", height: "75px", color: "#f0db4f"}}/>
                                    <SiTypescript style={{width: "50px", height: "75px", color: "#007acc"}}/>
                                    <FaReact style={{width: "50px", height: "75px", color: "#61DBFB"}}/>
                                    <FaNodeJs style={{width: "75px", height: "100px", color: "#3C873A"}}/>
                                    <SiMongodb style={{width: "75px", height: "100px", color: "#3FA037"}}/>
                                </div>
                                <h3>Used libraries</h3>
                                <ul>
                                    <li>axios</li>
                                    <li>react-router-dom</li>
                                    <li>styled-components</li>
                                    <li>react-icons</li>
                                    <li>express</li>
                                    <li>cors</li>
                                    <li>mongoose</li>
                                </ul>
                                <div className="public_img_div">
                                    <img src={Weather} style={ImgStyled}></img>
                                </div>
                            </div>
                        </div>


                        

                    </div>
                </div>
            </div>
        </Container>
    )
}

export default Home